import React, { useState } from 'react';
import {
  Button,
  Card,
  CardBody,
  CardTitle,
  FormGroup,
  Input,
  InputGroup,
  InputGroupAddon,
  Row,
} from 'reactstrap';
import { connect } from 'react-redux';
import IntlMessages from '../../../../helpers/IntlMessages';
import {
  Colxx,
  Separator,
} from '../../../../components/common/CustomBootstrap';
import Breadcrumb from '../../../../containers/navs/Breadcrumb';
import { adminRoot } from '../../../../constants/defaultValues';
import StateButton from '../../../../components/StateButton';
import instance from '../../../../helpers/axiosInstance';
import { setProfileData } from '../../../../helpers/Utils';

const Links = ({ match, profileData }) => {
  const [editLinks, setEditLinks] = useState(false);
  const { id, facebook, github, linkedin, instagram } = profileData;
  const [formValues, setFormValues] = useState({
    facebook,
    github,
    linkedin,
    instagram,
  });

  const handleChange = (event, key) => {
    setFormValues({
      ...formValues,
      [key]: event.target.value,
    });
  };

  const handleSubmitLinks = async (newData) => {
    return new Promise((success, failed) => {
      const updateProfile = async () => {
        const response = await instance({
          method: 'POST',
          url: '/updateProfile',
          data: { id, ...newData },
        });

        const { data } = response;
        if (data.success) {
          setProfileData({ ...profileData, ...newData });
          success('Profile updated successfully!');
        } else {
          failed('Failed to update profile!');
        }
      };
      updateProfile();
    });
  };

  return (
    <>
      <Row>
        <Colxx xxs="12">
          <Breadcrumb heading="menu.links" match={match} />
          <Separator className="mb-5" />
        </Colxx>
      </Row>

      <Row className="justify-content-center">
        <Colxx xxs="12" md="8">
          <Card className="mb-4">
            <div className="position-absolute card-top-buttons">
              <Button
                outline
                color="primary"
                onClick={() => {
                  setEditLinks(!editLinks);
                }}
                className="icon-button"
              >
                <i className="simple-icon-pencil" />
              </Button>
            </div>
            <CardBody>
              <CardTitle>
                <h2 className="lead">
                  <IntlMessages id="user.update-links" />
                </h2>
                <p className="mt-2">
                  <IntlMessages id="user.update-links-desc" />
                </p>
              </CardTitle>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-linkedin btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    className="form-control"
                    name="linkedin"
                    value={formValues.linkedin}
                    onChange={(event) => {
                      handleChange(event, 'linkedin');
                    }}
                    disabled={!editLinks}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-facebook btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="facebook"
                    value={formValues.facebook}
                    onChange={(event) => {
                      handleChange(event, 'facebook');
                    }}
                    disabled={!editLinks}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-github btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="github"
                    className="form-control"
                    value={formValues.github}
                    onChange={(event) => {
                      handleChange(event, 'github');
                    }}
                    disabled={!editLinks}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="mb-6">
                <InputGroup>
                  <InputGroupAddon addonType="prepend">
                    <i className="simple-icon-social-instagram btn" />
                  </InputGroupAddon>
                  <Input
                    type="text"
                    name="github"
                    className="form-control"
                    value={formValues.instagram}
                    onChange={(event) => {
                      handleChange(event, 'instagram');
                    }}
                    disabled={!editLinks}
                  />
                </InputGroup>
              </FormGroup>

              <FormGroup className="text-center">
                {editLinks && (
                  <StateButton
                    id="submitLinks"
                    color="primary"
                    type="submit"
                    onClick={() => handleSubmitLinks(formValues)}
                    next={() => {
                      setEditLinks(false);
                    }}
                  >
                    <IntlMessages id="forms.submit" />
                  </StateButton>
                )}
              </FormGroup>
            </CardBody>
          </Card>
        </Colxx>
      </Row>
      <Row>
        <Colxx>
          <FormGroup className="text-center">
            <Button
              color="primary"
              className="m-3"
              disabled={editLinks}
              href={`${adminRoot}/profile/experience`}
            >
              <IntlMessages id="forms.previous" />
            </Button>
            <Button
              color="primary"
              className="m-3"
              disabled={editLinks}
              href={`${adminRoot}/profile/contact`}
            >
              <IntlMessages id="forms.next" />
            </Button>
          </FormGroup>
        </Colxx>
      </Row>
    </>
  );
};

const mapStateToProps = ({ authUser }) => {
  const { currentUser, profileData } = authUser;
  return { currentUser, profileData };
};

const mapActionsToProps = {};

export default connect(mapStateToProps, mapActionsToProps)(Links);
